import * as React from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import './styles.css';

const ListNotes = () => {
    const [notes, setNotes] = React.useState(null);
    const [text, setText] = React.useState('');
    const [error, setError] = React.useState(null);
    const [trigger, setTrigger] = React.useState(false);
    const [offset, setOffset] = React.useState(1);
    const [limit, setLimit] = React.useState(1);


    React.useEffect(() => {
        axios.get(`/api/notes?offset=${offset}&limit=${limit}`)
            .then(res => res.data)
            .then(data => setNotes(data.notes))
            .catch(err => setError(err.message))
    }, [trigger, limit, offset]);

    const handleChange = (e) => {
        setText(e.target.value);
    }

    const handleSubmit = () => {
        axios.post('/api/notes', { text })
            .then(res => setTrigger(!trigger))
    }

    return (
        error
            ? <div className="container">{error}</div>
            : (
                <div className="container">
                    <div>
                        <h4>Set Query</h4>
                        <label htmlFor="offset">Offset: </label>
                        <input type="number" id="offset" value={offset} onChange={e => setOffset(e.target.value)} min={0} />
                        <label htmlFor="limit">Limit: </label>
                        <input type="number" id="limit" value={limit} onChange={e => setLimit(e.target.value)} min={0} />
                    </div>
                    <hr />
                    <div>
                        <h4>Add a new todo</h4>
                        <label htmlFor="text">Text: </label>
                        <input type="text" value={text} onChange={handleChange} />
                        <button onClick={handleSubmit} disabled={!text}>Add a new todo</button>
                    </div>
                    <hr />
                    <h4>List of todos</h4>
                    <ul>
                        {
                            notes && notes.map(note => {
                                return (
                                    <li key={note._id} ><Link to={`/notes/${note._id}`}>{note.text}</Link></li>
                                )
                            })
                        }
                    </ul>
                </div>
            )
    )
}

export default ListNotes;