import * as React from 'react';
import axios from 'axios';
import {
  BrowserRouter as Router,
  Route,
  Switch,
  Link,
} from 'react-router-dom';
import Login from './pages/Login';
import Profile from './pages/Profile';
import Register from './pages/Register';
import ProtectedRoute from './ProtectedRoute';
import ListNotes from './pages/ListNotes';
import SingleTodo from './pages/SingleTodo';

function App() {
  const [token, setToken] = React.useState(null);

  React.useEffect(() => {
    axios.defaults.headers.common['jwt_token'] = token;
  }, [token]);

  return (
    <Router>
      <nav>
        <ul>
          <li>
            <Link to="/">Profile</Link>
          </li>
          <li>
            <Link to="/register">Register</Link>
          </li>
          <li>
            <Link to="/login">Login</Link>
          </li>
          <li>
            <Link to="/notes">Notes</Link>
          </li>
        </ul>
      </nav>
      <Switch>
        <Route exact path='/register'>
          <Register />
        </Route>
        <Route exact path='/login'>
          <Login setToken={setToken} />
        </Route>
        <ProtectedRoute exact path='/' component={Profile} token={token} />
        <ProtectedRoute exact path='/notes' component={ListNotes} token={token} />
        <ProtectedRoute exect path='/notes/:id' component={SingleTodo} token={token} />
      </Switch>
    </Router>
  );
}

export default App;
