import * as React from 'react';
import axios from 'axios';
import { useHistory } from 'react-router-dom';
import './styles.css';

const Profile = () => {
    const [profileInfo, setProfileInfo] = React.useState(null);
    const [display, setDisplay] = React.useState('none');
    const [oldPassword, setOldPassword] = React.useState('');
    const [newPassword, setNewPassword] = React.useState('');
    const history = useHistory();


    React.useEffect(() => {
        axios.get('/api/users/me')
            .then(res => res.data)
            .then(data => setProfileInfo(data.user))
            .catch(err => console.log(err));

        console.log(profileInfo);
    }, []);

    const handleClick = () => {
        display === 'none'
            ? setDisplay('block')
            : setDisplay('none')
    }

    const handlePasswordChange = () => {
        axios.patch('/api/users/me', { oldPassword, newPassword })
            .then(res => { })
            .catch(err => { })
    }

    const handleDeleteUser = () => {
        axios.delete('/api/users/me')
            .then(res => history.push('/register'))
            .catch(err => { })
    }

    return (
        profileInfo ? (
            <div className="container">
                <h3> Username: {profileInfo.username}</h3 >
                <h3>CreatedDate: {profileInfo.createdDate}</h3>
                <h3>Id: {profileInfo._id}</h3>
                <button onClick={handleClick}>show profile info as plain text</button>
                <p style={{ display: display }}>
                    {JSON.stringify(profileInfo)}
                </p>
                <hr />
                <label htmlFor="oldPassword">Old Password: </label>
                <input type="text" id="oldPassword" value={oldPassword} onChange={e => setOldPassword(e.target.value)} />
                <label htmlFor="newPassword">New Password: </label>
                <input type="text" id="newPassword" value={newPassword} onChange={e => setNewPassword(e.target.value)} />
                <button onClick={handlePasswordChange} disabled={!oldPassword || !newPassword}>Submit</button>
                <hr />
                <button onClick={handleDeleteUser} >Delete user</button>
            </div>

        )
            : null
    )
}

export default Profile;