import * as React from 'react';
import axios from 'axios';
import { useHistory, useParams } from 'react-router-dom';
import './styles.css';

const SingleTodo = ({ token }) => {
    const [todo, setTodo] = React.useState(null);
    const [text, setText] = React.useState(null);
    const [trigger, setTrigger] = React.useState(false);
    const { id } = useParams();
    const history = useHistory();

    React.useEffect(() => {
        axios.get(`/api/notes/${id}`)
            .then(res => res.data)
            .then(data => {
                setTodo(data.note);
            })
            .catch(err => console.log(err));
    }, [id, trigger]);

    const handleTodoChange = () => {
        axios.put(`/api/notes/${id}`, { text })
            .then(res => setTrigger(!trigger));
    }

    const handleTodoToggle = () => {
        axios.patch(`/api/notes/${id}`)
            .then(res => setTrigger(!trigger));
    }

    const handleTodoDelete = () => {
        axios.delete(`/api/notes/${id}`)
            .then(res => history.push('/notes'))
    }


    return (
        <div className="container" >
            <h4>Todo Page</h4>
            {
                todo ?
                    <>
                        <h2>{todo.text}</h2>
                        <hr />
                        <label htmlFor="text">Text: </label>
                        <input type="text" value={text} onChange={e => setText(e.target.value)} placeholder="enter new text" />
                        <button onClick={handleTodoChange} >Save</button>
                        <hr />
                        <label htmlFor="completed">Completed: </label>
                        <input type="checkbox" id="completed" checked={todo.completed} onClick={handleTodoToggle} />
                        <button onClick={handleTodoDelete} >Delete todo</button>
                    </>
                    : null
            }
        </div>
    )
}

export default SingleTodo;