import React from "react";
import { Redirect, Route } from "react-router-dom";
import jwt from 'jsonwebtoken';

function ProtectedRoute({ component: Component, token, ...restOfProps }) {
  token = token ? token : 'ryJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJ1c2VybmFtZSI6Ikt5cnlsbyIsIl9pZCI6IjYxMjlmOTgzMjkxOWI0ZTc3ZmMzZWI3MSIsImlhdCI6MTYzMDE0MDgyMywiZXhwIjoxNjMwMTU4ODIzfQ.lmvgJXuBPQQFRrsEYWbrYDPe7UII2pJEFoi2BOA1bgo';
  
  return jwt.verify(token, process.env.REACT_APP_JWT_SECRET, (err, decoded) => {
    if (err) {
      return <Route {...restOfProps} render={() => <Redirect to='/login' />} />
    } else {
      return <Route {...restOfProps} render={(props) => <Component {...props} token={token} />} />
    }
  });
}

export default ProtectedRoute;
